.ClassItem {
    color:black;
  
    h3 {
      text-align: center;
    }
  
    .fade {
      background-color: #000;
      opacity: .50;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
    }
    .fade:hover {
      opacity: .15;
    }
  
    .name {
      font-size: 24px;
      text-align: center;
      width: 100%;
      z-index: 100;
      pointer-events: none;
    }
  
    .heading {
      font-weight: bold;
      text-transform: uppercase;
      text-align: center;
      }
    
      .power-title {
        font-weight: bold;
        text-decoration: underline;
      }
  
      .content {
        margin-bottom: 12px;
      }
    
      .content div {
        margin-bottom: 6px;
      } 
  
  }